@import './../../common/style/colors';
@import './../../common/style/fonts';
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&amp;subset=cyrillic');

.main {
	margin-bottom: 20px;
	&-news {
		position: relative;
		height: 320px;
		box-shadow: 0 10px 40px rgba(0,0,0,0.4);
		border-radius: 20px;
		overflow: hidden;
		&_section {
			margin-top: 20px;
		}
		&_link {
			display: block;
			height: 100%;
			position: relative;
			z-index: 2;
		}
		&-card {
			position: relative;
			width: 100%;
			height: 100%;
			background: linear-gradient(180deg, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.8) 100%);
			border-radius: 20px;

			&_img {
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: -1;
				background-position: center;
				background-repeat: no-repeat;
				background-size: cover;
				transform: scale(1);
				transition: all .3s ease-in-out;
				background-color: rgba(255,255,255,0.4);	
			}
			&_title {
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 30px;
				color: $colorTitle;
				font: $fontWeightTitle 20px $fontFamily;
				letter-spacing: $letterSpacing;
			}
		}
		&:hover {
			.main-news-card_img {
				transform: scale(1.05);
			}
		}
	}
	.news {
		position: relative;
		height: 320px;
		box-shadow: 0 5px 20px rgba(0,0,0,0.4);
		border-radius: 20px;
		overflow: hidden;
		margin-top: 20px;
		&_section {
			margin-top: 20px;
		}
		&_link {
			display: block;
			height: 100%;
			position: relative;
			z-index: 2;
		}
		&-card {
			position: relative;
			width: 100%;
			height: 100%;
			background: linear-gradient(180deg, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.8) 100%);
			border-radius: 20px;
			
			&_img {
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: -1;
				background-position: center;
				background-repeat: no-repeat;
				background-size: cover;
				transform: scale(1);
				transition: all .3s ease-in-out;
				background-color: rgba(255,255,255,0.4);
			}
			&_title {
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 30px;
				color: $colorTitle;
				font: $fontWeightTitle 20px $fontFamily;
				letter-spacing: $letterSpacing;
			}
		}
		&:hover {
			.news-card_img {
				transform: scale(1.05);
			}
		}
	}
}
.card-info {
	position: absolute;
	bottom: 100%;
	left: 0;
	&_category {
		font-size: 15px;
		display: inline-block;
		&::after {
			content: "•";
			margin-left: 5px;
		}
	}
	&_date {
		font-size: 15px;
		display: inline-block;
		margin-left: 5px;
	}
}
@media (min-width: 768px) {
	.main {
		&-news {
			height: 440px;
			&-card_title {
				font-size: 30px;
			}
		}
	}
}
@media (min-width: 1024px) {
	.main {
		&-news {
			&-card_title {
				font-size: 36px;
			}
		}
	}
}
@media (min-width: 1440px) {
	.main {
		&-news {
			&-card_title {
				font-size: 40px;
			}
		}
	}
}
@media (min-width: 2560px) {
	.main {
		&-news {
			&-card_title {
				font-size: 46px;
			}
		}
	}
}