@import './../../common/style/colors';
@import './../../common/style/fonts';
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&amp;subset=cyrillic');

.footer {
	background: $bgHeader;
	padding: 10px 0;
	text-align: center;
	&-logo {
		display: inline-block;
		color: $colorLogo;
		font: $fontWeightLogo $fontSizeLogo $fontFamily;
		letter-spacing: $letterSpacing;

		&_img {
			margin-right: 10px; 
		}
		&_span {
			color: #86BE26;
		}
		&:hover {
			color: $colorLogo;
			text-decoration: none;
		}
	}
	&-menu {
		line-height: 49px;
		&_links {
			display: block;
			margin: 10px 0;
			color: $colorMenuLink;
			font: $fontWeightMenu $fontSizeMenu $fontFamily;
			&:hover, &:focus {
				text-decoration: none;
				opacity: 0.7;
				color: $colorMenuLink;
				transition: 0.5s;
			}
		}
	}
	&-social {
		text-align: center;
		line-height: 49px;
		&_links {
			display: inline-block;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			background: $bgBody;
			line-height: 30px;
			margin: 0 5px;
			color: $bgHeader;
			&:hover {
				color: red;
				transition: 0.3s;
			}
		}
	} 
}
@media (min-width: 768px) {
	.footer {
		&-menu {
			text-align: center;
			&_links {
				display: inline-block;
				margin: 0 10px;
			}
		}
	}
}