@import './../../common/style/colors';
@import './../../common/style/fonts';
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&amp;subset=cyrillic');

body {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}
* {
	box-sizing: border-box;
}
::-webkit-scrollbar {
	width: 12px;
}
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(26, 26, 26, 0.1); 
	border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    border-radius: 10px;
	box-shadow: inset 0 0 6px rgba(26, 26, 26, 0.8);
}
::-webkit-scrollbar-thumb:hover {
	background: rgba(26, 26, 26, 0.1);
}

.header {
	background: $bgHeader;
	padding: 10px 0;
	position: sticky;
	top: 0;
	left: 0;
	right: 0;
	z-index: 10;

	&_logo {
		display: inline-block;
		color: $colorLogo;
		font: $fontWeightLogo $fontSizeLogo $fontFamily;
		letter-spacing: $letterSpacing;

		&-img {
			margin-right: 10px; 
		}
		&-span {
			color: #86BE26;
		}
		&:hover, &:focus {
			color: $colorLogo;
			text-decoration: none;
		}
	}
	&_burger {
		position: relative;
		width: 24px;
		height: 24px;
		display: block;
		margin: 12.5px auto;

		span, span::before, span::after{
			display: block;
			width: 20px;
			height: 2px;
			background: #ffffff;
			position: absolute;
			top: 50%; margin-top: -1px;
			left: 50%; margin-left: -10px;
			transition: 0.5s;
			border-radius: 5px;
		}
		span::before, span::after {
			display: block;
			content: "";
		}
		span {
			transform: translateY(-5px);
		}
		span::before {
			transform: translateY(5px);
		}
		span::after {
			transform: translateY(10px);
		}
	}
	&_burger_toggle {
		display: block;
		span {
			transform: rotate(-45deg);
			transform-origin: center;
		}
		span::before {
			transform: scale(0);
		}
		span::after {
			transform: rotate(90deg);
		}
	}
	&-search_btn {
		position: relative;
		width: 24px;
		height: 24px;
		margin: 12.5px auto;
		color: #ffffff;
		font-size: 15px;
		text-align: center;

		&_toggle {
			background: none;
			span, span::after {
				visibility: visible;
				width: 20px;
				height: 2px;
				position: absolute;
				top: 50%; margin-top: -1px;
				left: 50%; margin-left: -10px;
				transform: rotate(45deg);
				transform-origin: center;
				background: #FDFDFD;
				transition: transform 0.5s;
			}
			span::after {
				content: "";
				transform: rotate(90deg);
			}
		}
	}
}
.menu {
	visibility: hidden;
	position: fixed;
	height: 91vh;
	width: 100vw;
	background: $bgMenu;
	transform: translateY(-50%) scale(0);
	transform-origin: right top;
	transition: linear 0.5s;
	z-index: 5;
	
	&_toggle {
		visibility: visible;
		transform: translateY(0) scale(1);
		transform-origin: right top;
	}
	.header_menu {
		text-align: center;
		padding: 50% 0;

		&-link {
			display: block;
			color: $colorMenuLink;
			font: $fontWeightMenu $fontSizeMenu $fontFamily;
			margin-bottom: 15px;
		}
		&-link:hover, &-link:focus {
			opacity: 0.7;
			text-decoration: none;
			transition: .3s;
		}
	}
}
.block-search {
	background: $bgHeader;
	padding: 15px 0;
	position: absolute;
	width: 100%;
	visibility: hidden;
	transform: translateY(-50%) scale(0);
	transform-origin: right center;
	transition: 0.5s;
	z-index: 5;
	&_input {
		background: transparent;
		border: 0;
		border-bottom: 1px solid $colorSearch;
		width: 100%;
		color: $colorSearch;
		font: $fontWeightSearch $fontSizeSearch $fontFamily;
		padding: 10px;
		outline: none;
	}
	&_btn {
		width: 40px;
		height: 40px;
		border: 0;
		padding: 0;
		background: transparent;
		color: #ffffff;
		outline: none;
	}
	&_open {
		visibility: visible;
		transform: translateY(0) scale(1);
		transition: 0.5s;
	}
}
@media (min-width: 768px) {
	.header {
		position: relative;
	}
}
@media (min-width: 1024px) {
	.header {
		position: relative;
		&_burger {
			display: none;
		}
		&-search_btn {
			left: 100%;
		}
	}
	.menu {
		visibility: visible;
		position: relative;
		transform: translateY(0);
		height: 100%;
		width: inherit;
		.header_menu {
			padding: 10px 0;
			&-link {
				display: inline-block;
				margin-bottom: 0;
				margin: 0 20px;
			}
		}
	}
}