@import './../../common/style/colors';
@import './../../common/style/fonts';
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&amp;subset=cyrillic');

.newsPage {
    position: relative;
    &-title {
        font: $fontWeightTitle $fontSizeTitle $fontFamily;
        letter-spacing: $letterSpacing;
        margin: 10px 0;
    }
    &-category, &-date {
        display: inline-block;
        font: $fontWeightCategory $fontSizeCategory $fontFamily;
        color: $colorCategory;
        margin: 5px 5px;
    }
    &-img {
        margin: 20px 0;
        width: 100%;
        border-radius: 20px;
    }
    &-description {
        font: $fontWeightDescription $fontSizeDescription $fontFamily;
        letter-spacing: $letterSpacing;
        margin: 0;
    }
}
blockquote {
    background: #F9F9F9;
    padding: 30px;
}
b {
    font-size: 29px;
}