body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif; }

* {
  box-sizing: border-box; }

::-webkit-scrollbar {
  width: 12px; }

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(26, 26, 26, 0.1);
  border-radius: 10px; }

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(26, 26, 26, 0.8); }

::-webkit-scrollbar-thumb:hover {
  background: rgba(26, 26, 26, 0.1); }

.header {
  background: #232323;
  padding: 10px 0;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10; }
  .header_logo {
    display: inline-block;
    color: #FDFDFD;
    font: 700 30px "Roboto";
    letter-spacing: 0.3px; }
    .header_logo-img {
      margin-right: 10px; }
    .header_logo-span {
      color: #86BE26; }
    .header_logo:hover, .header_logo:focus {
      color: #FDFDFD;
      text-decoration: none; }
  .header_burger {
    position: relative;
    width: 24px;
    height: 24px;
    display: block;
    margin: 12.5px auto; }
    .header_burger span, .header_burger span::before, .header_burger span::after {
      display: block;
      width: 20px;
      height: 2px;
      background: #ffffff;
      position: absolute;
      top: 50%;
      margin-top: -1px;
      left: 50%;
      margin-left: -10px;
      transition: 0.5s;
      border-radius: 5px; }
    .header_burger span::before, .header_burger span::after {
      display: block;
      content: ""; }
    .header_burger span {
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px); }
    .header_burger span::before {
      -webkit-transform: translateY(5px);
              transform: translateY(5px); }
    .header_burger span::after {
      -webkit-transform: translateY(10px);
              transform: translateY(10px); }
  .header_burger_toggle {
    display: block; }
    .header_burger_toggle span {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
      -webkit-transform-origin: center;
              transform-origin: center; }
    .header_burger_toggle span::before {
      -webkit-transform: scale(0);
              transform: scale(0); }
    .header_burger_toggle span::after {
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg); }
  .header-search_btn {
    position: relative;
    width: 24px;
    height: 24px;
    margin: 12.5px auto;
    color: #ffffff;
    font-size: 15px;
    text-align: center; }
    .header-search_btn_toggle {
      background: none; }
      .header-search_btn_toggle span, .header-search_btn_toggle span::after {
        visibility: visible;
        width: 20px;
        height: 2px;
        position: absolute;
        top: 50%;
        margin-top: -1px;
        left: 50%;
        margin-left: -10px;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        -webkit-transform-origin: center;
                transform-origin: center;
        background: #FDFDFD;
        transition: -webkit-transform 0.5s;
        transition: transform 0.5s;
        transition: transform 0.5s, -webkit-transform 0.5s; }
      .header-search_btn_toggle span::after {
        content: "";
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg); }

.menu {
  visibility: hidden;
  position: fixed;
  height: 91vh;
  width: 100vw;
  background: #2a2a2a;
  -webkit-transform: translateY(-50%) scale(0);
          transform: translateY(-50%) scale(0);
  -webkit-transform-origin: right top;
          transform-origin: right top;
  transition: linear 0.5s;
  z-index: 5; }
  .menu_toggle {
    visibility: visible;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
    -webkit-transform-origin: right top;
            transform-origin: right top; }
  .menu .header_menu {
    text-align: center;
    padding: 50% 0; }
    .menu .header_menu-link {
      display: block;
      color: #FDFDFD;
      font: 700 23px "Roboto";
      margin-bottom: 15px; }
    .menu .header_menu-link:hover, .menu .header_menu-link:focus {
      opacity: 0.7;
      text-decoration: none;
      transition: .3s; }

.block-search {
  background: #232323;
  padding: 15px 0;
  position: absolute;
  width: 100%;
  visibility: hidden;
  -webkit-transform: translateY(-50%) scale(0);
          transform: translateY(-50%) scale(0);
  -webkit-transform-origin: right center;
          transform-origin: right center;
  transition: 0.5s;
  z-index: 5; }
  .block-search_input {
    background: transparent;
    border: 0;
    border-bottom: 1px solid #5b5b5b;
    width: 100%;
    color: #5b5b5b;
    font: 400 17px "Roboto";
    padding: 10px;
    outline: none; }
  .block-search_btn {
    width: 40px;
    height: 40px;
    border: 0;
    padding: 0;
    background: transparent;
    color: #ffffff;
    outline: none; }
  .block-search_open {
    visibility: visible;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
    transition: 0.5s; }

@media (min-width: 768px) {
  .header {
    position: relative; } }

@media (min-width: 1024px) {
  .header {
    position: relative; }
    .header_burger {
      display: none; }
    .header-search_btn {
      left: 100%; }
  .menu {
    visibility: visible;
    position: relative;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    height: 100%;
    width: inherit; }
    .menu .header_menu {
      padding: 10px 0; }
      .menu .header_menu-link {
        display: inline-block;
        margin-bottom: 0;
        margin: 0 20px; } }

